import React from "react"
import "./tags.css"


// receives a tags array as children returns inlined tags
const Tags = (props) => {
    const tags = props.children;
    return (
        <>
        {tags.map((tag) => {
            return (
                <div key={tag} className="tags"> {tag} </div>
            )})
        }
        </>
    )
}

export default Tags;
